@tailwind base;
@tailwind components;
@tailwind utilities;

@layer utilities {
	.mask::after {
		content         : "";
		position        : absolute;
		inset           : 0;
		background-color: rgba(0, 0, 0, 0.2);
		/* 50% black overlay */
		z-index         : 1;
		pointer-events  : none;
	}

	.mask img {
		position: relative;
		z-index : 0;
	}
}